* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.welcome {
  height: 50vh;
  border: .5em double tomato;
  justify-content: center;
  align-items: center;
  display: flex;
}

h1 {
  color: tomato;
}

section {
  color: #c71e00;
  background: #ffe5e0;
  border: 2px solid;
  margin: 2em;
  padding: 1em;
}

section p, section h2 {
  margin-bottom: 1em;
}

section:hover {
  background: #ffb6c1;
}

section.first img {
  float: left;
  margin: 0 1em 1em 0;
}

section.second {
  flex-wrap: wrap;
  gap: 1em;
  display: flex;
}

section.second h2 {
  flex-basis: 100%;
}

section.second img {
  flex-grow: 0;
  flex-basis: 100px;
}

section.second > * {
  flex-grow: 1;
  flex-basis: 33%;
}

section.third img {
  position: sticky;
  top: 10px;
}

